<template>
  <div class="kefu">
    <!-- 顶部 -->
    <header class="header1">
      <div class="hd-1">
        <div class="title-bar">
          <label class="title-bar_l">
            <span class="icon_1" @click="toIndex"></span>
          </label>
          <div class="title-bar-h-l">
            <div class="title-bar-h-l_1">
              <div class="title-bar-h-l_2">尚品宅配</div>
            </div>
          </div>
        </div>
      </div>
    </header>
    <!-- 主体聊天栏 -->
    <main>
      <nut-scroller
        :is-un-more="isUnMore2"
        :is-loading="isLoading2"
        :type="'vertical'"
        @pulldown="pulldown2"
      >
        <div slot="list" class="nut-vert-list-panel">
          <div class="nut-vert-list-item">
            <dl class="nut-scroller-item-info">
              <dt class="nut_title">{{msg_}}</dt>
              <div class="window_Container" v-for="(item,index) in wclist" :key="index">
                <div class="window_MsgContainer">
                  <div class="base_Wrapper">
                    <img class="wrap_img" src="../../assets/imgs/default-avatar.jpg" alt />
                    <div class="msg-wrapper_">
                      <div class="msg-base_time">{{time_}}</div>
                      <div class="msg-base_MsgWrapper">
                        <div class="msg_msgBox">
                          <div class="msg-rich_RichWrapper">{{item.msg}}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div
                class="msg-wrapper_ msg-wrapper_1"
                v-for="(item1,index1_ ) in msg9"
                :key="index1_ + '-label'"
              >
                <div class="msg-base_time">{{time_}}</div>
                <div class="msg-base_MsgWrapper msg-base_MsgWrapper1">
                  <div class="msg_msgBox">
                    <div class="msg-rich_RichWrapper">{{item1}}</div>
                  </div>
                </div>
              </div>
            </dl>
          </div>
        </div>
      </nut-scroller>
      <div class="chat_bottom">
        <div class="cb_input">
          <div class="action-box-mobile">
            <div class="action-tooltip_">
              <div class="action-tooltip_BtnsWrap">
                <div class="action-tooltip__Btns">
                  <label class="icon_span">
                    <span class="base-icon"></span>
                  </label>
                  <label class="icon_span">
                    <span class="base-icon1"></span>
                  </label>
                  <label class="icon_span">
                    <span class="base-icon2"></span>
                  </label>
                </div>
              </div>
              <div class="action-tooltip__Custom">
                <div class="action-box-mobile_input">
                  <div class="action-box-mobile_TextareaWrap">
                    <div class="chat-textarea_Container">
                      <form action>
                        <input
                          type="text"
                          placeholder="请输入"
                          autocomplete="off"
                          class="action-textarea__Input"
                          v-model="msg1"
                        />
                      </form>
                    </div>
                  </div>
                  <div class="action-box-mobile_ButtonWrap">
                    <a href="javascript:;" class="button__Anchor">
                      <span @click="sendMsg">发送</span>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data () {
    return {
      count: 0,
      isLoading: false,
      time_: '11:02',
      flag: false,
      msg1: '',
      msg9_1: 0,
      msg9: ['你好'
      ],
      listData: new Array(20),
      listData1: new Array(20),
      msg_: '加载更多历史消息',
      listData2: [1, 2],
      listData3: [1],
      maxPages: 4,
      isUnMore1: false,
      isLoading1: false,
      wclist: [
        { msg: '欢迎光临【尚品宅配】我是您的家居顾问，了解预约免费设计，可留下您的联系方式+姓名+城市，我们会优先为您安排' },
        { msg: '【系统消息】很高兴能为您服务，希望可以帮助到您，祝您生活愉快！' }
      ],
      page1: 2,
      maxPages2: 1,
      isUnMore2: false,
      isLoading2: false,
      page2: 2,
      timer: null
    }
  },
  created () {
    this.time_ = new Date().toLocaleTimeString()
  },
  methods: {
    toIndex () {
      this.$router.push({ path: '/index' })
    },
    loadMoreVert () {
      this.isLoading1 = true
      if (this.page1 > this.maxPages) {
        this.isUnMore1 = true
        this.isLoading1 = false
      } else {
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.isLoading1 = false
          this.isUnMore1 = false
        }, 300)
      }
    },
    pulldown () {
      this.isLoading1 = true
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.isLoading1 = false
        this.isUnMore1 = false
      }, 3000)
    },
    loadMoreVert2 () {
      this.isLoading2 = true
      if (this.page2 > this.maxPages2) {
        this.isUnMore2 = true
        this.isLoading2 = false
      } else {
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.isLoading2 = false
          this.isUnMore2 = false
        }, 300)
      }
    },
    pulldown2 () {
      this.isLoading2 = true
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.isLoading2 = false
        this.isUnMore2 = false
      }, 300)
      this.msg_ = ''
    },
    destroyed () {
      clearTimeout(this.timer)
    },
    sendMsg () {
      if (this.msg1 === '') {
        return
      } else {
        this.msg9.push(this.msg1)
      }
      this.msg1 = ''
      this.msg9_1 += this.msg9_1
    }
  }
}
</script>
<style lang="scss" scoped>
.kefu {
  width: 100%;
  height: 100%;
  .header1 {
    .hd-1 {
      position: fixed;
      top: 0px;
      width: 100%;
      z-index: 20;
      .title-bar {
        width: 100%;
        height: 55px;
        display: flex;
        -webkit-box-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        align-items: center;
        font-size: 14px;
        box-sizing: border-box;
        color: white;
        padding: 10px;
        background: rgb(179, 28, 19);
        .title-bar_l {
          display: inline-block;
          vertical-align: middle;
          margin-left: 5px;
          cursor: pointer;
          // width: 24px;
          // height: 30px;
          opacity: 0.8;
          .icon_1 {
            vertical-align: middle;
            padding-right: 24px;
            height: 30px;

            display: inline-block;
            background: url('../../assets/imgs/icon-mobile@2x.png') -25px -436px /
              44px no-repeat;
            background-position: -25px -436px;
          }
        }
      }
    }
    .title-bar-h-l {
      height: 40px;
      position: relative;
      flex: 10 1 auto;
      overflow: hidden;
      .title-bar-h-l_1 {
        width: 100%;
        display: inline-block;
        vertical-align: middle;
        .title-bar-h-l_2 {
          font-size: 12px;
          line-height: 40px;
        }
      }
    }
  }
}
main {
  height: 100%;
  width: 100%;
  margin-top: 56px;
  .nut_title {
    position: absolute;
    top: -10px;
    left: 0px;
    right: 0px;
    width: 100%;
    height: 20px;
    line-height: 20px;
    text-align: center;
    font-size: 12px;
    color: rgb(119, 119, 119);
    cursor: pointer;
    z-index: 5;
  }
}
.window_Container {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  position: relative;
  padding-top: 0px;
  background: rgb(252, 252, 252);
  margin-top: 12px;
}
.window_MsgContainer {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow-y: auto;
  position: relative;
  padding: 20px 14px 14px;
  margin-top: 20px;
  .window_MsgContainer {
    width: 100%;
    position: relative;
    .base_Wrapper {
      padding-top: 10px;
      padding-bottom: 10px;
      padding-right: 10px;
    }
  }
}
.wrap_img {
  width: 36px;
  height: 36px;
  margin-right: 10px;
  margin-top: 22px;
  vertical-align: top;
  float: left;
}
.msg-wrapper_ {
  float: left;
  text-align: left;
  max-width: calc(100% - 50px);
  .msg-base_time {
    color: rgb(178, 178, 178);
    font-size: 12px;
    margin-bottom: 2px;
    padding-left: 5px;
  }
  .msg-base_MsgWrapper {
    max-width: 100%;
    position: relative;
    background-color: rgb(255, 255, 255);
    float: left;
    border-radius: 3px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(240, 240, 240);
    border-image: initial;
    .msg_msgBox {
      font-size: 13px;
      word-break: break-word;
      padding: 10px 15px;
      border-radius: 3px;
      .msg-rich_RichWrapper {
        width: 100%;
        display: inline-block;
        overflow: hidden;
        font-size: 14px;
      }
    }
  }
}
.chat_bottom {
  width: 100%;
  position: fixed;
  left: 0px;
  right: 0px;
  bottom: 0px;
  .cb_input {
    width: 100%;
    height: 50px;
    background: rgb(252, 252, 252);
    .action-box-mobile {
      height: 100%;
      width: 100%;
      position: relative;
      box-sizing: border-box;
      border-top: 1px solid rgb(237, 237, 237);
      .action-tooltip_ {
        position: relative;
        height: 50px;
        box-sizing: border-box;
        display: flex;
        flex-direction: row;
        padding: 5px 10px 5px 14px;
        .action-tooltip_BtnsWrap {
          display: flex;
          -webkit-box-align: center;
          align-items: center;
          -webkit-box-flex: 0;
          flex-grow: 0;
          flex-wrap: nowrap;
          height: 55px;
          padding-bottom: 25px;
          .action-tooltip__Btns {
            height: 20px;
            display: flex;
            flex-wrap: nowrap;
            .icon_span {
              vertical-align: middle;
              margin-right: 6px;
              cursor: pointer;
              opacity: 0.8;
              .base-icon {
                vertical-align: middle;
                padding-right: 20px;
                height: 20px;
                display: inline-block;
                background: url('../../assets/imgs/icon-desktop@2x.png') -23px -1px /
                  44px no-repeat;
                background-position: -23px -1px;
              }
              .base-icon1 {
                vertical-align: middle;
                padding-right: 20px;
                height: 20px;
                display: inline-block;
                background: url('../../assets/imgs/icon-desktop@2x.png') -23px -23px /
                  44px no-repeat;
                background-position: -23px -23px;
              }
              .base-icon2 {
                vertical-align: middle;
                padding-right: 20px;
                height: 20px;
                display: inline-block;
                background: url('../../assets/imgs/icon-desktop@2x.png') -23px -45px /
                  44px no-repeat;
                background-position: -23px -45px;
              }
            }
          }
        }
        .action-tooltip__Custom {
          -webkit-box-flex: 1;
          flex-grow: 1;
          width: 100%;
          text-align: right;
          .action-box-mobile_input {
            width: 100%;
            height: 100%;
            padding-right: 50px;
            box-sizing: border-box;
            position: relative;
            .action-box-mobile_TextareaWrap {
              width: 100%;
              height: 100%;
              .chat-textarea_Container {
                width: 100%;
                height: 100%;
                position: relative;
                box-sizing: border-box;
                form {
                  display: block;
                  margin-top: 0em;
                  margin-block-end: 1em;
                  .action-textarea__Input {
                    display: block;
                    width: 100%;
                    height: 100%;
                    font-size: 13px;
                    line-height: 20px;
                    padding: 8px 10px;
                    outline: 0px;
                    background: rgb(255, 255, 255);
                    border-width: initial;
                    border-style: none;
                    border-color: initial;
                    border-image: initial;
                  }
                }
              }
            }
            .action-box-mobile_ButtonWrap {
              display: inline-block;
              height: 30px;
              width: 48px;
              line-height: 30px;
              text-align: center;
              position: absolute;
              right: 0px;
              top: 5px;
              bottom: 0px;
              .button__Anchor {
                width: auto;
                height: auto;
                display: block;
                font-size: 14px;
                cursor: pointer;
                text-align: center;
                opacity: 1;
                color: white;
                border-width: 0px;
                border-style: initial;
                border-color: initial;
                border-image: initial;
                outline: none;
                border-radius: 3px;
                text-decoration: none;
                background: rgb(179, 28, 19);
              }
            }
          }
        }
      }
    }
  }
}
.msg-wrapper_1 {
  display: block;
  width: 100%;
  float: right;
  text-align: right;
  margin-right: 15px;
  margin-bottom: 20px;
}
.msg-base_MsgWrapper1 {
  float: right !important;
}
</style>
