import Vue from 'vue'
import App from './App.vue'
import router from './router'
import 'amfe-flexible/index.js'
import MuseUI from 'muse-ui'
import Helpers from 'muse-ui/lib/Helpers'
import NutUI from '@nutui/nutui'
import '@nutui/nutui/dist/nutui.css'
import 'vant/lib/icon/local.css'
import 'vant/lib/index.css'

import 'muse-ui/dist/muse-ui.css'
import {
  Col,
  Row,
  Popup,
  Collapse,
  CollapseItem,
  Swipe,
  SwipeItem,
  Grid,
  GridItem,
  Tabbar,
  TabbarItem,
  Icon,
  Area,
  Tab,
  Tabs,
  NumberKeyboard,
  Field,
  PullRefresh
} from 'vant'

Vue.config.productionTip = false
Vue.use(Helpers)
Vue.use(Area)
Vue.use(Col)
Vue.use(Row)
Vue.use(Popup)
Vue.use(Collapse)
Vue.use(CollapseItem)
Vue.use(Swipe)
Vue.use(PullRefresh)
Vue.use(SwipeItem)
Vue.use(Grid)
Vue.use(GridItem)
Vue.use(MuseUI)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(Icon)
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(NumberKeyboard)
Vue.use(Field)
NutUI.install(Vue)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
