import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../components/Index.vue'
import '../assets/icon/iconfont.css'
import Kefu from '../components/kefu/Kefu.vue'
import Yuyue from '../components/yuyue/Yuyue.vue'
import Fuwu from '../components/fuwu/Fuwu.vue'
import Room from '../components/room/Room.vue'
// Vue.use(Col)
// Vue.use(Row)

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/kefu',
      name: 'kefu',
      component: Kefu
    },
    {
      path: '/fuwu',
      name: 'fuwu',
      component: Fuwu
    },
    {
      path: '/yuyue',
      name: 'yuyue',
      component: Yuyue
    },
    {
      path: '/room',
      name: 'room',
      component: Room
    }
  ]
})
export default router
